* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #0e0e2e;
  overscroll-behavior: none;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 6px;
}
body::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(37, 37, 128, 0.404);
}
body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(45deg, #e4ba9b, #a68eff);
  border-radius: 10px;
}

a {
  cursor: pointer;
}
.wrap {
  /* position: fixed; */
  width: 100%;
  height: 100%;
  /* overflow: auto; */
  /* scroll-behavior: smooth; */
}
html {
  /* scroll-behavior: smooth; */
}

