.fingerToggleContainer {
  bottom: -188px;
  width: 100%;
}

.fingerToggle {
  background: url('../assets/images/fingerDown.png') no-repeat;
  width: 87px;
  height: 179px;
}
.open .fingerToggle {
  background: url('../assets/images/fingerUp.png') no-repeat;
  width: 87px;
  height: 179px;
}
.fingerToggle:hover {
  animation: fingerUpEffect 0.1s linear forwards;
}
.open .fingerToggle:hover {
  animation: fingerDownEffect 0.1s linear forwards;
}

@keyframes fingerUpEffect {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(10px);
  }
}
@keyframes fingerDownEffect {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-10px);
  }
}
.storyListContainer {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.open .storyListContainer {
  max-height: 800px;
  overflow: auto;
}

.storyItemContainer {
  position: relative;
  display: flex;
  padding: 25px 0;
  margin: 0 10px;
  justify-content: space-between;
}
.storyItemContainer:not(:last-child) {
  border-bottom: 1px solid rgba(228, 186, 155, 0.5);
}
.storyItemContainer > .flex > img {
  margin-right: 25px;
}
.storyItemContainer .arrowRight:hover {
  animation: buttonEffect 0.1s linear forwards;
  transform-origin: left;
}
@keyframes buttonEffect {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.2);
  }
}
