  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.introducePeople .peopleContainer {
  display: flex;
  width: 40vw;
}
.introducePeople .peopleContainer img {
  width: 100%;
