  grid-column-start: 1;
}
.introducePeople .peopleContainer a {
  display: grid;
  align-items: flex-end;
}
.introducePeople .cardContainer {
  width: 50vw;
  display: grid;
}
.introducePeople .cardContainer img {
  width: 100%;
