  grid-column-start: 1;
}

.cardContainer > img {
  /* position: absolute; */
  animation: photoFadeOut 0.3s linear forwards;
}
.cardContainer > img.active {
  animation: photoFadein 0.3s linear forwards;
}

.introducePeople .humanContainer0 {
  flex: 15;
}
.introducePeople .humanContainer1 {
  flex: 13;
}
.introducePeople .humanContainer2 {
