}
.introducePeople .humanContainer3 {
  flex: 10;
}

.introduceFont {
  font-family: MaruBuriot;
  font-size: 30px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.81;
  letter-spacing: -0.85px;
  text-align: center;
  color: #e4ba9b;
}

.peopleContainer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.peopleContainer > a > img.photo {
  /* position: absolute; */
  animation: photoFadeOut 0.3s linear forwards;
}
.peopleContainer > a > img.line {
  animation: photoFadeIn 0.3s linear forwards;
}

.peopleContainer > a.active > img.photo {
  animation: photoFadein 0.3s linear forwards;
}
.peopleContainer > a.active > img.line {
  animation: photoFadeOut 0.3s linear forwards;
}

@keyframes photoFadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes photoFadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

